import React, { useState } from "react";
import { ContentTitle } from "../chapter3/titleLink";
import Title from "../common/Title";

// EventPractice6   
function EventPractice8() {
  // useState     
  const [form, setForm] = useState({
    username: "",
    message: "",
  });
  const { username, message } = form;
  const onChange = (e) => {
    const nextForm = {
      ...form, //  form  
      [e.target.name]: e.target.value, //   
    };
    setForm(nextForm);
  };

  const onClick = () => {
    alert(username + ": " + message);
    setForm({
      username: "",
      message: "",
    });
  };
  const onKeyPress = (e) => {
    if (e.key === "Enter") {
      onClick();
    }
  };
  return (
    <div>
      <Title Component={ContentTitle} id={"EventPractice8"} />
      <p>
        state       object 
         
      </p>
      <input
        type={"text"}
        name="username"
        placeholder=""
        value={username}
        onChange={onChange}
      />
      <input
        type={"text"}
        name="message"
        placeholder="  "
        value={message}
        onChange={onChange}
        onKeyPress={onKeyPress}
      />
      <button onClick={onClick}></button>
    </div>
  );
}

export default EventPractice8;
